.gpt__cta{ 
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;

    margin: 4rem;
    padding: 2rem;
    border-radius: 1rem;
    background: var(--gradient-bar);

}

.gpt__cta_content p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 250%;
    color: #0e0e0e;
}

.gpt__cta-btn h3{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 187%;
    color: #000;
}

.gpt__cta-btn button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
    border-radius: 40px;
    padding: .5rem 1rem;
    background: #000;

    font-family: var(--font-family);
    font-weight: 700;
    font-size: 18px;
    line-height: 167%;
    color: #fff;

    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}

@media screen and (max-width: 650px) {
    .gpt__cta{ 
        flex-direction: column;
    }

    .gpt__cta-btn{
        margin: 2rem 0 0;

    }
    
}

@media screen and (max-width: 550px) {
    .gpt__cta{
        margin: 4rem 2rem;
     }

     .gpt__cta-btn h3{
        font-size: 18px;
        line-height: 32px;
     }

     .gpt__cta-btn button {
        font-size: 16px;
        line-height: 28px;
     }

}